import namespace from './namespace';

export default {
    [namespace.mutations.SET_MAPPINGS_VALUES] (state, responseData) {
        if (responseData.mappings !== undefined) {
            state[namespace.state.MAPPINGS] = responseData.mappings;
        }
        if (responseData.available !== undefined) {
            state[namespace.state.AVAILABLE_VALUES] = responseData.available;
        }
    },
    [namespace.mutations.SHOW_LOADER] (state, loaderVisible) {
        state[namespace.state.LOADER_VISIBLE] = loaderVisible;
    }
};


