import Vue from 'vue';
import LinkMapping from './components/LinkMapping';
import ProfileHealth from './components/ProfileHealth';
import WizardSide from './components/WizardSide';
import NotificationHandler from './components/NotificationHandler';
import HealthStatusIndicator from './components/HealthStatusIndicator';
import MessageBox from './components/MessageBox';
import Hierarchy from './components/Hierarchy';
import Notification from './components/Notification';
import FieldsMapping from './components/FieldsMapping';
import SmtpConfiguration from './components/SmtpConfiguration';
import LoggingEvents from './components/LoggingEvents';
import UnsupportedBrowserChecker from './components/UnsupportedBrowserChecker';
import SupportSideBar from './components/SupportSideBar';
import WebinarStripe from './components/WebinarStripe';
import UsersMapping from './components/UsersMapping';

import store from './store';

Vue.config.productionTip = false;

Array.prototype.forEach.call(document.querySelectorAll('.vue-container'), (container) => {
	new Vue({
	  el: '#' + container.getAttribute("id"),
	  store,
	  components: { 
	  	LinkMapping,
	  	WizardSide,
	  	NotificationHandler,
	  	HealthStatusIndicator,
	  	ProfileHealth,
	  	MessageBox,
	  	Hierarchy,
	  	Notification,
	  	FieldsMapping,
	  	SmtpConfiguration,
		LoggingEvents,
		UnsupportedBrowserChecker,
	  	SupportSideBar,
	  	WebinarStripe,
	  	UsersMapping
	  },
	})
});


