import namespace from './namespace';

export default  {
    [namespace.getters.DEFAULT_VALUES]: (state) => {
        return state[namespace.state.DEFAULT_VALUES];
    },
    [namespace.getters.AVAILABLE_VALUES]: (state) => {
        let availableValues = state[namespace.state.AVAILABLE_VALUES];
        let jiraValues = [];
        let tfsValues = [];
        if (availableValues.length !== 0) {
            availableValues.forEach(value => value.visible = true);
            jiraValues = availableValues.filter(value => value.kind === 'JIRA');
            tfsValues = availableValues.filter(value => value.kind === 'TFS');
        }

        return { 
            TfsValues: tfsValues,
            JiraValues: jiraValues
        };
    },
    [namespace.getters.SPECIAL_VALUES]: (state) => {
        return state[namespace.state.SPECIAL_VALUES];
    },
    [namespace.getters.IS_LOADER_VISIBLE]: (state) => {
        return state[namespace.state.LOADER_VISIBLE];
    }
};


