import utils from './../utils';
import namespace from './smtp/namespace';
import actions from './smtp/actions';
import state from './smtp/state';
import getters from './smtp/getters';
import mutations from './smtp/mutations';

const STORE = {
    namespaced: true,
    state: state,
    getters: getters,
    actions: actions,
    mutations: mutations
};
const PREFIX = 'SmtpConfiguration';

const NAMESPACE = utils.publishNamespace(PREFIX, namespace);

export {
    STORE,
    PREFIX,
    NAMESPACE
};


