export default class UsersMappingService {
    constructor(http) {
        this.http = http;
    }

    getData() {
        return new Promise((resolve, reject) => {
            this.http.get(this.backendUrl + '/mappings/users').then(
                (response) => {
                    if (response.status === 200 && response.data) {
                        resolve(this.shapeDataFromWebService(response.data));
                    }
                },
                (err) => {
                    reject(err);
                }
            );
        });
    }

    createItem(kind, key, value) {
        return {
            id: key,
            kind: kind,
            key: key,
            label: value,
            visible: true
        }
    }

    shapeDataFromWebService(responseData) {
        let items = {};
        Array.prototype.forEach.call(responseData.JiraUsers, el => {
            let item = this.createItem('JIRA', el.Key, el.Value);
            items[item.id] = item;
        });

        Array.prototype.forEach.call(responseData.TfsUsers, el => {
            let item = this.createItem('TFS', el.Key, el.Value);
            items[item.id] = item;
        });
        return this.hideListItemsAndEnrichMappings(items, responseData.Mappings);
    }

    hideListItemsAndEnrichMappings(availableValues, mappings) {
        Object.values(availableValues).forEach(item => {
            item.visible = true;
        });

        mappings.forEach(mapping => {
            Object.values(availableValues).forEach(item => {
                if (item.key === mapping.JiraUserKey) {
                    item.visible = false;
                }
                if (item.key === mapping.TfsUserKey) {
                    item.visible = false;
                }
            });
            mapping.jiraTitle = mapping.JiraUserValue;
            mapping.tfsTitle = mapping.TfsUserValue;
            mapping.duringProcessing = false;
        });
        return {
            available: availableValues,
            mappings: mappings
        };
    }

    addMapping(jiraValue, tfsValue, availableValues, mappings) {
        return new Promise((resolve, reject) => {
            this.http.post(this.backendUrl + '/mappings/users', {
                JiraUserKey: jiraValue.key,
                JiraUserValue: jiraValue.label,
                TfsUserKey: tfsValue.key,
                TfsUserValue: tfsValue.label
            }).then(
                (response) => {
                    if (response.status === 200 && response.data) {
                        mappings.push(response.data);
                        resolve(this.hideListItemsAndEnrichMappings(availableValues, mappings));
                    }
                },
                (err) => {
                    reject(err);
                }
            )
        });
    }

    removeMapping(availableValues, mappings, mappingObject) {
        return new Promise((resolve, reject) => {
            this.http.delete(this.backendUrl + '/mappings/users/' + mappingObject.Id).then(
                (response) => {
                    if (response.status === 200 && response.data === true) {
                        mappings = mappings.filter(mapping => mapping.Id !== mappingObject.Id);
                        resolve(this.hideListItemsAndEnrichMappings(availableValues, mappings));
                    }
                },
                (err) => {
                    reject(err);
                }
            )
        });
    }

    getDefaultValues() {
        return new Promise((resolve, reject) => {
            this.http.get(this.backendUrl + '/mappings/users/defaults').then(
                (response) => {
                    if (response.status === 200 && response.data) {
                       resolve(this.wrapDefaultValues(response.data));
                    }
                },
                (err) => {
                    reject(err);
                }
            );
        });
    }

    wrapDefaultValues(responseData) {
        let defaultValues = responseData.DefaultValues;

        const createDefaultValue = (value) => {
            return {
                JiraValue: this.createItem('JIRA', value.JiraUserKey, value.JiraUserValue),
                TfsValue: this.createItem('TFS', value.TfsUserKey, value.TfsUserValue)
            }
        }

        let items = [];
        Array.prototype.forEach.call(responseData.SpecialValues, el => {
            let jiraItem = this.createItem('JIRA', el.Key, el.Value);
            items.push(jiraItem);
            let tfsItem = this.createItem('TFS', el.Key, el.Value);
            items.push(tfsItem);
        });

        return {
            DefaultValues: createDefaultValue(defaultValues),
            SpecialValues: items
        }
    }

    updateDefaultValues(defaultValues) {
        return new Promise((resolve, reject) => {
            this.http.put(this.backendUrl + '/mappings/users/defaults', defaultValues).then(
                (response) => {
                    if (response.status === 204) {
                        resolve();
                    }
                },
                (err) => {
                    reject(err);
                }
            );
        });
    }
}


