import utils from './../../utils.js';

export default utils.generateNamespace({
    state: [
        'AVAILABLE_VALUES',
        'LOADER_VISIBLE',
        'MAPPINGS'
    ],
    actions: [
        'SET_UP_SERVICE',
        'GET_AVAILABLE_VALUES',
        'MAP',
        'REMOVE_MAPPING',
        'SWITCH_TFS_LINK',
        'HANDLE_ERROR'
    ],
    mutations: [
        'SHOW_LOADER',
        'SET_MAPPINGS_VALUES'
    ],
    getters: [
        'AVAILABLE_VALUES',
        'TFS_VALUES',
        'JIRA_VALUES',
        'MAPPINGS',
        'IS_LOADER_VISIBLE'
    ],
    kinds: {
        JIRA: 'JIRA',
        TFS: 'TFS'
    }
});


