<style scoped>
    @import '../assets/css/supportSideBar.css';
</style>
<template>
    <div class="side-bar">
        <div>
            <a href="https://jira.spartez.com/servicedesk/customer/portal/2" target="_blank" title="Submit a feature request or report a bug" @click="sendEvent(SubmitFeature)">
                <icon name="comments"></icon>
            </a>
            <a href="mailto:support@spartez.com" title="Contact us" @click="sendEvent(ContactUs)">
                <icon name="envelope"></icon>
            </a>
            <a href="https://confluence.spartez.com/display/TFS4JIRA/TFS4JIRA+Documentation+Home" target="_blank" title="TFS4JIRA documentation" @click="sendEvent(ReadDocumentation)">
                <icon name="book"></icon>
            </a>
        </div>
    </div>
</template>
<script>
    import Vue from 'vue';
    import { mapGetters, mapActions } from 'vuex';
    import { NAMESPACE as SUPPORT_ACTIVITY } from '../store/modules/supportActivity.js';
    import { NAMESPACE as APP_NAMESPACE } from '../store/modules/app.js';
    import 'vue-awesome/icons/book';
    import 'vue-awesome/icons/envelope';
    import 'vue-awesome/icons/comments';
    import Icon from 'vue-awesome/components/Icon.vue';
    export default {
        data() {
            return {
                SubmitFeature: {
                    SupportType: 'Submit feature request or report bug',
                    EventType: 'Clicked'
                },
                ContactUs: {
                    SupportType: 'Contact us (mailto)',
                    EventType: 'Clicked'  
                },
                ReadDocumentation: {
                    SupportType: 'Link to TFS4JIRA documentation',
                    EventType: 'Clicked'
                }
            }
        },
        components: {
            'icon': Icon
        },
        methods: {
            ...mapActions({
                setUpService: SUPPORT_ACTIVITY.actions.SET_UP_SERVICE,
                sendEvent: SUPPORT_ACTIVITY.actions.SEND_EVENT
            }),
            ...mapGetters({
                contextPath: APP_NAMESPACE.getters.CONTEXT_PATH
            })
        },
        mounted: function() {
            this.setUpService({
                prefix: this.contextPath() + '/api/profiles'
            });
        }
    }
</script>


