import utils from './../utils';
import namespace from './support/namespace';
import actions from './support/actions';

const STORE = {
    namespaced: true,
    actions: actions
};
const PREFIX = 'supportActivity';

const NAMESPACE = utils.publishNamespace(PREFIX, namespace);

export {
    STORE,
    PREFIX,
    NAMESPACE
};


