import utils from './../../utils.js';

export default utils.generateNamespace({
    state: [
        'DEFAULT_VALUES',
        'AVAILABLE_VALUES',
        'SPECIAL_VALUES',
        'LOADER_VISIBLE'
    ],
    actions: [
        'SET_UP_SERVICE', 
        'GET_DEFAULT_VALUES',
        'SET_AVAILABLE_VALUES',
        'UPDATE_DEFAULT_VALUES',
        'HANDLE_ERROR'
    ],
    mutations: [
        'SET_DEFAULT_VALUES',
        'SET_AVAILABLE_VALUES',
        'SHOW_LOADER' 
    ],
    getters: [
        'DEFAULT_VALUES',
        'AVAILABLE_VALUES',
        'SPECIAL_VALUES',
        'IS_LOADER_VISIBLE'
    ]
});


