import namespace from './namespace';

export default  {
    [namespace.getters.LOGGING_EVENTS]: (state) => {
        return state[namespace.state.LOGGING_EVENTS];
    },
    [namespace.getters.WAITING_FOR_RESPONSE]: (state) => {
        return state[namespace.state.WAITING_FOR_RESPONSE];
    },
    [namespace.getters.IDS_ARRAY_OF_ROWS_WAITING_FOR_DELETE]: (state) => {
        return state[namespace.state.IDS_ARRAY_OF_ROWS_WAITING_FOR_DELETE];
    },
    [namespace.getters.IS_SMTP_CONFIGURATION_ENABLED]: (state) => {
        return state[namespace.state.IS_SMTP_CONFIGURATION_ENABLED];
    }
};


