import utils from './../utils';
import namespace from './usersMapping/namespace';
import actions from './usersMapping/actions';
import state from './usersMapping/state';
import getters from './usersMapping/getters';
import mutations from './usersMapping/mutations';

const STORE = {
    namespaced: true,
    state: state,
    getters: getters,
    actions: actions,
    mutations: mutations
};
const PREFIX = 'usersMapping';

const NAMESPACE = utils.publishNamespace(PREFIX, namespace);

export {
    STORE,
    PREFIX,
    NAMESPACE
};


