import utils from './../utils';
import namespace from './loggingEvents/namespace';
import actions from './loggingEvents/actions';
import state from './loggingEvents/state';
import getters from './loggingEvents/getters';
import mutations from './loggingEvents/mutations';

const STORE = {
    namespaced: true,
    state: state,
    getters: getters,
    actions: actions,
    mutations: mutations
};
const PREFIX = 'loggingEvents';

const NAMESPACE = utils.publishNamespace(PREFIX, namespace);

export {
    STORE,
    PREFIX,
    NAMESPACE
};


