<style scoped>
    .default-mapping-row {
        margin: 0 auto;
        width: 75%;
    }
    .fieldlistleft, .fieldlistright {
        padding: 10px 0;
    }
    .tfs4jira-mapping-row-direction-jira-to-tfs, 
    .tfs4jira-mapping-row-direction-tfs-to-jira {
        background-repeat: no-repeat;
        background-size: 80px 20px;
        display: block;
        height: 20px;
        width: 80px;
        margin: 0 auto;
    }
</style>
<template>
    <div>
        <div class="default-mapping-row">
            <div class="fieldlistleft">
                <select class="defaultvalue" id="default-jira-value" v-model="defaultJiraValue">
                    <optgroup label="Action">
                        <option v-for="value in jiraSpecialValues" :key="value.key" :value="value">
                            {{value.label}}
                        </option>
                    </optgroup>
                    <optgroup label="Set value">
                        <option v-for="value in availableValues.JiraValues" :key="value.key" :value="value">
                            {{value.label}}
                        </option>
                    </optgroup>
                </select>
            </div>
            <div class="fieldlistmiddle">
                <div class="tfs4jira-mapping-row-direction-tfs-to-jira"></div>
            </div>
            <div class="fieldlistright"><i>anything else</i></div>
        </div>
        <div class="default-mapping-row">
            <div class="fieldlistleft"><i>anything else</i></div>
            <div class="fieldlistmiddle">
                <div class="tfs4jira-mapping-row-direction-jira-to-tfs"></div>
            </div>
            <div class="fieldlistright">
                <select class="defaultvalue" id="default-tfs-value" v-model="defaultTfsValue">
                    <optgroup label="Action">
                        <option v-for="value in tfsSpecialValues" :key="value.key" :value="value">
                            {{value.label}}
                        </option>
                    </optgroup>
                    <optgroup label="Set value">
                        <option v-for="value in availableValues.TfsValues" :key="value.key" :value="value">
                            {{value.label}}
                        </option>
                    </optgroup>
                </select>
            </div>
        </div>
    </div>
</template>
<script>
    import { mapGetters, mapActions } from 'vuex';
    import axios from 'axios';
    import { NAMESPACE as USERS_MAPPING_NAMESPACE } from '../store/modules/usersMapping.js';
    import UserMappingsService from './../service/usersMapping.js';

    export default {
        props: {
            profileGuid: String,
            contextPath: String,
            loadOnInit: String
        },
        computed: {
            ...mapGetters({
                defaultValues: USERS_MAPPING_NAMESPACE.getters.DEFAULT_VALUES,
                availableValues: USERS_MAPPING_NAMESPACE.getters.AVAILABLE_VALUES,
                specialValues: USERS_MAPPING_NAMESPACE.getters.SPECIAL_VALUES
            }),
            defaultJiraValue: {
                get: function() {
                    if(this.defaultValues.JiraValue) {
                        return this.defaultValues.JiraValue;
                    }
                },
                set: function(newValue) {
                    if (newValue) {
                        this.updateDefaultValues({
                            JiraUserKey: newValue.key,
                            JiraUserValue: newValue.label,
                            TfsUserKey: this.defaultTfsValue.key,
                            TfsUserValue: this.defaultTfsValue.label
                        });
                    }
                }
            },
            defaultTfsValue: {
                get: function() {
                    if(this.defaultValues.TfsValue) {
                        return this.defaultValues.TfsValue;
                    }
                },
                set: function(newValue) {
                    if (newValue) {
                        this.updateDefaultValues({
                            JiraUserKey: this.defaultJiraValue.key,
                            JiraUserValue: this.defaultJiraValue.label,
                            TfsUserKey: newValue.key,
                            TfsUserValue: newValue.label
                        });
                    }
                }
            },
            jiraSpecialValues: {
                get: function() {
                    return this.specialValues.filter(value => value.kind === 'JIRA');
                }
            },
            tfsSpecialValues: {
                get: function() {
                    return this.specialValues.filter(value => value.kind === 'TFS');
                }
            }
        },
        methods: {
            ...mapActions({
                setUpService: USERS_MAPPING_NAMESPACE.actions.SET_UP_SERVICE,
                getDefaultValues: USERS_MAPPING_NAMESPACE.actions.GET_DEFAULT_VALUES,
                updateDefaultValues: USERS_MAPPING_NAMESPACE.actions.UPDATE_DEFAULT_VALUES
            }),
            initComponent: async function() {
                this.setUpService({
                    prefix: this.contextPath + '/api/profiles/' + this.profileGuid,
                    service: new UserMappingsService(axios)
                });
                await this.getDefaultValues();
            },
        },
        watch: {
            loadOnInit: {
                handler(newValue, oldValue) {
                    if (newValue != oldValue && newValue === 'true') {
                        this.initComponent();
                    }
                }
            }
        },
        mounted: function() {
            if (this.loadOnInit !== undefined && this.loadOnInit === 'true') {
                this.initComponent();
            }
        }
    }
</script>


