<style scoped>
    @import '../assets/css/webinarStripe.css';
</style>
<template>    
    <div class="stripe" v-show="!shouldHideWebinarStripe && !stripeHiddenByUserManually">
        <a class="hide" @click="hide()"><icon name="close"></icon></a>
        <p>Sign up for 1 of the 2 free webinars & get a 10% discount for license purchase</p>
        <div class="buttons-bar">
            <a href="https://campaigns.spartez.com/tfs4jiragetthemost?src=tfs4jirasynchronizer" target="parent">New to TFS4JIRA? Get the most out of your evaluation</a>
            <a href="https://campaigns.spartez.com/tfs4jirawhatscoming?src=tfs4jirasynchronizer" target="parent">Already using TFS4JIRA? Learn what's coming up</a>
        </div>
    </div>
</template>
<script>
    import { NAMESPACE as APP_NAMESPACE } from '../store/modules/app.js';
    import { mapActions, mapGetters } from 'vuex';
    import Icon from 'vue-awesome/components/Icon.vue';
    import 'vue-awesome/icons/close';

    const WEBINAR_STRIPE_HIDDEN_BY_USER_COOKIE_NAME = 'WEBINAR_STRIPE_HIDDEN_BY_USER';
    const LIFETIME_UNTIL_DATE = new Date('2019-02-25');

    export default {
        data() {
            return {
                stripeHiddenByUserManually: false
            }
        },
        components: {
            Icon
        },
        computed: {
            ...mapGetters({
                cookieStore: APP_NAMESPACE.getters.GET_COOKIE_STORE,
                imagePath: APP_NAMESPACE.getters.IMAGE_PATH,
            }),
            shouldHideWebinarStripe: function() {
                if (typeof(this.cookieStore) !== 'function') {
                    return true;
                }
                const passedLifeTime = (new Date() > LIFETIME_UNTIL_DATE);
                return this.cookieStore(WEBINAR_STRIPE_HIDDEN_BY_USER_COOKIE_NAME) || passedLifeTime;
            }
        },
        methods: {
            hide: function() {
                this.cookieStore(WEBINAR_STRIPE_HIDDEN_BY_USER_COOKIE_NAME, true, {path: '/', expires: 30});
                this.stripeHiddenByUserManually = true;
            }
        }
    }
</script>


